import "./App.css";
import React, { useEffect, useRef, useState } from "react";
import { BANUBA_CLIENT_TOKEN } from "./BanubaClientToken";
import { Webcam, Player, Module, Effect, Dom } from "@banuba/webar";
import wasm from "@banuba/webar/BanubaSDK.wasm";
import simd from "@banuba/webar/BanubaSDK.simd.wasm";
import data from "@banuba/webar/BanubaSDK.data";
import FaceTracker from "@banuba/webar/face_tracker.zip";
import lips from "@banuba/webar/lips.zip";
import eyes from "@banuba/webar/eyes.zip";
import skin from "@banuba/webar/skin.zip";
import productsData from './data/products.json';

function App() {
  const ref = useRef({});
  const [effect, setEffect] = useState(null);
  const [cameraActive, setCameraActive] = useState(false);
  const [popupVisible, setPopupVisible] = useState(false);
  const [selectedFeature, setSelectedFeature] = useState(null);
  const [loading, setLoading] = useState(false);
  const [showMoreFeatures, setShowMoreFeatures] = useState(false); // State to toggle between more features

  const [makeupState, setMakeupState] = useState({
    lips: { color: "#FF0000", enabled: false },
    eyes: { color: "#000000", enabled: false },
    eyeshadow: { color: "#8B4513", enabled: false },
    eyeliner: { color: "#000000", enabled: false },
    eyelashes: { color: "#000000", enabled: false },
    brows: { color: "#4A3728", enabled: false },
    blush: { color: "#FF6B6B", enabled: false },
    skin: { color: "#FFE4C4", enabled: false, softening: 0.5 },
  });

  const hexToRGBA = (hex, alpha = 1) => {
    hex = hex.replace("#", "");
    const r = parseInt(hex.substring(0, 2), 16);
    const g = parseInt(hex.substring(2, 4), 16);
    const b = parseInt(hex.substring(4, 6), 16);
    return `${r / 255} ${g / 255} ${b / 255} ${alpha}`;
  };

  const updateMakeup = async (feature, updates) => {
    if (!effect) return;

    try {
      const newState = { ...makeupState[feature], ...updates };
      setMakeupState((prev) => ({
        ...prev,
        [feature]: newState,
      }));

      const alpha = newState.enabled ? 1 : 0;
      const color = hexToRGBA(newState.color, alpha);

      switch (feature) {
        case "lips":
          await effect.evalJs(`Lips.color("${color}")`);
          break;
        case "eyes":
          await effect.evalJs(`Eyes.color("${color}")`);
          break;
        case "eyeshadow":
          await effect.evalJs(`Makeup.eyeshadow("${color}")`);
          break;
        case "eyeliner":
          await effect.evalJs(`Makeup.eyeliner("${color}")`);
          break;
        case "eyelashes":
          await effect.evalJs(`Eyelashes.color("${color}")`);
          break;
        case "brows":
          await effect.evalJs(`Brows.color("${color}")`);
          break;
        case "blush":
          await effect.evalJs(`Makeup.blush("${color}")`);
          break;
        case "skin":
          if (newState.enabled) {
            await effect.evalJs(`Skin.color("${hexToRGBA(newState.color, 0.5)}")`);
            await effect.evalJs(`Skin.softening(${newState.softening})`);
          } else {
            await effect.evalJs(`Skin.color("0 0 0 0")`);
            await effect.evalJs(`Skin.softening(0)`);
          }
          break;
      }
    } catch (error) {
      console.error(`Error updating ${feature}:`, error);
    }
  };

  useEffect(() => {
    if (cameraActive) {
      const webcam = (ref.current.webcam = new Webcam());
      const player = (ref.current.player = Player.create({
        clientToken: BANUBA_CLIENT_TOKEN,
        locateFile: {
          "BanubaSDK.wasm": wasm,
          "BanubaSDK.simd.wasm": simd,
          "BanubaSDK.data": data,
        },
      }));

      setLoading(true);

      player.then(async (player) => {
        await player.addModule(new Module(FaceTracker));
        await player.addModule(new Module(lips));
        await player.addModule(new Module(eyes));
        await player.addModule(new Module(skin));

        player.use(webcam);

        const makeupEffect = new Effect("effects/Makeup_new_morphs.zip");
        await player.applyEffect(makeupEffect);
        setEffect(makeupEffect);

        const webarElement = document.getElementById("webar");
        if (webarElement) {
          Dom.render(player, webarElement);
        }

        // Apply initial makeup states
        if (selectedFeature) {
          updateMakeup(selectedFeature, makeupState[selectedFeature]);
        }

        setLoading(false);

      });

      return () => {
        webcam.stop();
        const webarElement = document.getElementById("webar");
        if (webarElement) {
          Dom.unmount(webarElement);
        }
        setEffect(null);
        ref.current.player = null;
        ref.current.webcam = null;
      };
    }
  }, [cameraActive, selectedFeature]);

  const handleOpenCamera = (feature) => {
    setSelectedFeature(feature);
    setShowMoreFeatures(false); // Initially only show selected feature
    setCameraActive(true);
    setPopupVisible(true);
  };

  const handleCloseCamera = () => {
    setCameraActive(false);
    setPopupVisible(false);
    Dom.unmount("#webar");
    setEffect(null);
    setSelectedFeature(null);
  };

  const toggleMoreFeatures = () => {
    setShowMoreFeatures((prev) => !prev);
  };

  const MakeupControl = ({ feature, label }) => {
    const state = makeupState[feature];

    // Get colors for the selected feature
    const product = productsData.find((product) => product.feature === feature);
    const colors = product ? product.colors : ["#FF0000", "#000000"]; // Default colors if none found

    const handleColorSelect = (color) => {
      updateMakeup(feature, { color, enabled: true });
    };

    return (
      <div className="makeup-control">
        <h4>{label}</h4>
        <div className="color-options">
          {colors.map((color, index) => (
            <div
              key={index}
              className="color-swatch"
              style={{ backgroundColor: color }}
              onClick={() => handleColorSelect(color)}
            ></div>
          ))}
        </div>
      </div>
    );
  };

  return (
    <div className="App">
      <header>
        <h1>Cosmetic Try-On Website</h1>
      </header>
      {!popupVisible ? (
        // Initial screen with "Try On" button
        <div className="product-grid">
          {productsData.map((product, index) => (
            <div key={index} className="product-card">
              <img className="" src={`${product.img}`} />
              <h3>{product.name}</h3>
              <p>{product.price !== '' ? product.price : 'Free'}</p>
              <button
                className="tryon-btn"
                onClick={() => handleOpenCamera(product.feature)}
              >
                Try On
              </button>
            </div>
          ))}
        </div>
      ) : (
        <div className="popup">
          {loading && (
            <div className="loader">
              <div className="spinner"></div>
            </div>
          )}

          <div id="webar" style={{ maxWidth: "100%" }}></div>
          <button
            className="close-btn"
            onClick={handleCloseCamera}
            style={{
              position: "fixed",
              top: "10px",
              right: "10px",
              zIndex: 1,
              padding: "10px",
              fontSize: "16px",
            }}
          >
            Close
          </button>

          <div className="feature-section">
            {selectedFeature && !showMoreFeatures && (
              <MakeupControl
                feature={selectedFeature}
                label={selectedFeature.charAt(0).toUpperCase() + selectedFeature.slice(1)}
              />
            )}

            {showMoreFeatures && (
              <div className="more-features">
                {productsData.map((product) => (
                  <MakeupControl
                    key={product.feature}
                    feature={product.feature}
                    label={product.feature.charAt(0).toUpperCase() + product.feature.slice(1)}
                  />
                ))}
              </div>
            )}
          </div>

          <button
            className="show-all-feature"
            onClick={toggleMoreFeatures}
            style={{ zIndex: 1, padding: "10px", fontSize: "16px" }}
          >
            {showMoreFeatures ? "Show Less Features" : "Show All Features"}
          </button>

        </div>
      )}
      <footer>
        <p>&copy; 2024 Cosmetic Website. All rights reserved.</p>
      </footer>
    </div>
  );
}

export default App;
